body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to top, rgb(251, 194, 235) 0%, rgb(166, 193, 238) 100%) !important;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  font-size: 16px;
  height: 100vh;
  min-height: 100%;
  max-width: 100%;
  margin: auto;
  background-repeat: no-repeat;
  overflow: hidden;
}

#root {
  display: block;
  margin: auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  vertical-align: middle;
  text-align: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  max-width: 405px;
  min-width: 320px;
  max-height: 720px;
  overflow: auto;
  background-image: linear-gradient(-225deg, rgb(35, 21, 87) 0%, rgb(80 0 162) 29%, rgb(21 55 175) 67%, rgb(107 106 72) 100%);
}

.app_wrapper {
  padding: 25px;
}

.main_btn {
  display: inline-block;
  border: 1px solid #FFFFFF;
  background: #2b2b2b !important;
  background: linear-gradient(120deg, rgb(253, 251, 251) 0%, rgb(235, 237, 238) 100%) !important;
  border: none !important;
  background: -o-linear-gradient(120deg, rgb(253, 251, 251) 0%, rgb(235, 237, 238) 100%) !important;
  border: none !important;
  background: -moz-linear-gradient(120deg, rgb(253, 251, 251) 0%, rgb(235, 237, 238) 100%) !important;
  border: none !important;
  background: -webkit-linear-gradient(120deg, rgb(253, 251, 251) 0%, rgb(235, 237, 238) 100%) !important;
  border: none !important;
  background: -ms-linear-gradient(120deg, rgb(253, 251, 251) 0%, rgb(235, 237, 238) 100%) !important;
  border: none !important;
  width: 65%;
  margin: 0 auto;
  max-width: 300px;
  padding: 15px 0;
  text-align: center;
  border-radius: 35px;
  color: #151515 !important;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 16px !important;
}

.slick-slide {
  outline: none;
}

.slick-arrow {
  position: absolute;
  top: calc(50% - 15px);
  font-size: 0;
  border: none;
  width: 20px;
  height: 20px;
  z-index: 100;
  cursor: pointer;
  outline: none;
  border: solid #fff;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  
  background: none;
}

.slick-arrow.slick-prev {
  left: -20px;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.slick-arrow.slick-next {
  right: -20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.slick-arrow.slick-disabled {
  opacity: 0.5;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

