.frame_wrapper {
    background: #000;
    position: relative;
    width: 275px;
    height: 275px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    transition: all .1s linear;
    border-radius: 20px;
    margin: 0 auto;
    overflow: hidden;
}

.frame_wrapper img {
    position: relative;
    z-index: 10;
}

.mainNav {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-align-content: stretch;
    -ms-flex-line-pack: stretch;
    align-content: stretch;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
}

.mainNav a {
    color: #fff;
    padding: 5px 20px;
    text-decoration: none;
    border-radius: 10px;
    margin: 0 10px;
    border: 1px solid #fff;
    transition: all 0.3s ease;
}

.mainNav a:hover {
    opacity: 0.7;
}

.uploadlink {
    text-decoration: underline;
    cursor: pointer;
}

.frame_wrapper_overlay {
    position: absolute;
    z-index: 1000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}